.vedio{
  position: relative;
  width:100%;
  height:100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.v1{
  position: absolute;
  margin-top:-100px;
  /* left:0;
  bottom:0;
  right:0; */
   z-index: -1; 
  width:100%;
  height:100vh;
}
 
@media screen and (max-width:1000px) and (min-width:700px){
  .v1{
          width:100%;
          height:auto;
  }
